import styles from 'Word.module.css'

interface WordProps {
  num: number
  word: string
  place: string
  condition: string
  flags: boolean[]
  handleClick: () => void
}

const Word: React.VFC<WordProps> = ({num, word, place, condition, flags, handleClick}) => {
  return (
    <div>
      {flags[num] ? <span>{word}</span> : <span>{word}()</span>}
      <button onClick={handleClick}></button>
      {/*flagstrue*/}
      <span>
        {place}{condition}
      </span>
    </div>
  )
}

export default Word
